import React from "react";
import { createRoot } from "react-dom/client";
import { HashRouter, Routes, Route } from "react-router-dom";
import * as Sentry from '@sentry/react';

import "./css/fonts.css";
import "./css/root.css";

import Versioncheck from "./pages/versioncheck.jsx";
import Auth from "./pages/auth.jsx";
import Main from "./pages/main.jsx";
import Settings from "./pages/settings.jsx";
import Library from "./pages/library.jsx";
import Downloads from "./pages/downloads.jsx";
import Leaderboards from "./pages/leaderboard.jsx";
import Offline from "./pages/offline.jsx";

Sentry.init({
    dsn: "https://9c85382debe1a4fa51b246687b2bd013@sentry.lunarfn.org/3",
    integrations: [
        Sentry.browserTracingIntegration(),
        Sentry.replayIntegration(),
    ],
    tracesSampleRate: 1.0,
    tracePropagationTargets: ["localhost"],
    replaysSessionSampleRate: 0.1,
    replaysOnErrorSampleRate: 1.0
})

console.log("Setup Sentry!");

const root = createRoot(document.getElementById("root"));
const App = () => {
    return (
        <HashRouter>
            <Routes>
                <Route path="/" exact element={<Versioncheck />} />
                <Route path="/auth" element={<Auth />} />
                <Route path="/main" element={<Main />} />
                <Route path="/settings" element={<Settings />} />
                <Route path="/library" element={<Library />} />
                <Route path="/downloads" element={<Downloads />} />
                <Route path="/leaderboard" element={<Leaderboards />} />
                <Route path="/offline" element={<Offline />} />
            </Routes>
        </HashRouter>
    );
};

root.render(<App />);
