import React from "react";
import Frame from "../components/frame.jsx";
import Sidebar from "../components/Main/SideBar.jsx";
import MiniShop from "../components/Main/MiniShop.jsx";
import Note from "../components/Main/Note.jsx";
import News from "../components/Main/News.jsx";
import LaunchTab from "../components/Main/LaunchTab.jsx";
import KillsCard from "../components/Main/KillsCard.jsx";
import WinsCard from "../components/Main/WinsCard.jsx";
import MatchesCard from "../components/Main/MatchesCard.jsx";
import BattlePassCard from "../components/Main/BattlePassCard.jsx";
import Friends from "../components/Main/Friends.jsx";
import { motion } from "framer-motion";
import "../css/Main/main.css";

export default function Main() {
  return (
    <div className="main-container">
      <Sidebar />
      <Frame />
      <motion.div
        initial={{ x: 150, opacity: 0 }}
        animate={{ x: 0, opacity: 1 }}
        transition={{
          type: "spring",
          stiffness: 200,
          damping: 15,
          duration: 0.3,
        }}
        className="content-container"
      >
        <Friends/>
        <KillsCard />
        <WinsCard />
        <MatchesCard />
        <BattlePassCard />
        <LaunchTab />
        <News />
        <Note />
        <MiniShop />
      </motion.div>
    </div>
  );
}
