import React from "react";
import "../../css/Main/HelpCard.css";

const { shell } = window.require("electron");

const HelpCard = () => {
  return (
    <div className="help-card">
      <div className="question-icon"><i className="fa-brands fa-discord"></i></div>
      <div className="content">
        <h2>Need help?</h2>
        <p>Join our <span className="bold" >Support</span> server and get some help!</p>
        <button className="discord-button" onClick={() => shell.openExternal("https://discord.gg/p6hGwXV6")}>Discord</button>
      </div>
    </div>
  );
}

export default HelpCard;
