import React, { useEffect, useState } from "react";
import axios from "axios";
import "../../css/Main/Modal.css";
import CircularProgress from "@mui/material/CircularProgress";
import { motion } from "framer-motion";

const { ipcRenderer } = window.require("electron");

const Modal = ({ isVisible, onClose }) => {
  const [progress, setProgress] = useState(0);
  const [versionInfo, setVersionInfo] = useState("");
  const [shortVersionInfo, setShortVersionInfo] = useState("");

  const imageUrl =
    "https://cdn.lunarfn.org/client/v3/assets/FortniteRelease.jpg";

  useEffect(() => {
    ipcRenderer.on("UpdateLoadingBar", (event, callback) => setProgress(callback));

    const fetchVersionInfo = async () => {
      try {
        // todo (secret) make another route on launcher-service.v3.prod.lunarfn.org
        // so we dont have to use the sha512 checking route.
        const { data } = await axios.get(
          "https://launcher-service.v3.prod.lunarfn.org/game/api/hashes/version_hashes"
        );

        const sha512Key =
          "c4d9ea517000301135ee01ba8bdeebb6ce9e980e1b1d8fd6a3e85063e81db91c690cdaa13c31008d43971cf9fdbd817a0bbf770a8df66db840e4399cded793c6";
        const sha512Data = data.SHA512[sha512Key];

        setVersionInfo(sha512Data.version_extended);
        setShortVersionInfo(sha512Data.version);
      } catch (error) {
        console.error("Error fetching version info:", error);
      }
    };

    fetchVersionInfo();
  }, []);

  return (
    isVisible && (
      <div className="modal-overlay" onClick={onClose}>
        <motion.div
          initial={{ x: 150, opacity: 0 }}
          animate={{ x: 0, opacity: 1 }}
          exit={{ x: 150, opacity: 0 }}
          transition={{
            type: "spring",
            stiffness: 200,
            damping: 15,
            duration: 0.3,
          }}
          className="modal-content"
          onClick={(e) => e.stopPropagation()}
        >
          <div className="modal-header">
            <div className="header-image-container">
              <img src={imageUrl} alt="Header" className="header-image" />
              <h2>Fortnite {shortVersionInfo || "Loading..."}</h2>
              <p className="Small-h2">{versionInfo || "Loading..."}</p>
            </div>
          </div>
          <div className="modal-body">
            <p className="Launch">
              Launching...{" "}
              <CircularProgress
                className="LoadingCircle"
                sx={{ color: "#f5f5f5" }}
                size={20}
              />
            </p>
            <div className="progress-bar-Modal">
              <div
                className="progress-Modal"
                style={{ width: `${progress}%` }}
              ></div>
            </div>
          </div>
        </motion.div>
      </div>
    )
  );
};

export default Modal;