import React from "react"
import Frame from "../components/frame.jsx";
import Sidebar from "../components/Main/SideBar.jsx";
import DownloadPath from "../components/Settings/DownloadPath.jsx";
import ReloadButton from "../components/Settings/Reload.jsx";
import LogoutHud from "../components/Settings/LogoutHud.jsx";
import ClearButton from "../components/Settings/ClearData.jsx";
import Spacer from "../components/Settings/Spacer.jsx";
import Themes from "../components/Settings/Themes.jsx";
import CheckForUpdatesbutton from "../components/Settings/CheckForUpdatesBtn.jsx";
import { motion } from "framer-motion";

export default function Settings() {
    return (
        <div className="Main">
            <Frame />
            <Sidebar />
            <motion.div
        initial={{ x: 150, opacity: 0 }}
        animate={{ x: 0, opacity: 1 }}
        transition={{
          type: "spring",
          stiffness: 200,
          damping: 15,
          duration: 0.2,
        }}
        className="Settings-content"
>
  <CheckForUpdatesbutton /> 
  <Spacer /> 
  <Themes />
  <LogoutHud />
  <DownloadPath />
  <ReloadButton />
  <ClearButton />
</motion.div>
  </div>
    )
}