import React, { useEffect, useState } from "react";
import "../../css/Main/MatchesCard.css";

import * as MCP from "../../utils/MCP.jsx"

const MatchesCard = () => {
  const [played, setPlayed] = useState(0);

  useEffect(() => {
    const fetchMatched = async () => {
      const data = await MCP.DownloadAthenaEntry();
      setPlayed(data.matched_played);
    }

    fetchMatched();
  }, [])

  return (
    <div className="Matches-card">
      <span className="Matches-value">{played}</span>
      <div className="Matches-label">Matches Played</div>
      <div className="Matches-icon">
        <i className="fa-solid fa-book-bookmark"></i>
      </div>
    </div>
  );
};

export default MatchesCard;
