import React, { useEffect, useState, useRef } from "react";
import axios from "axios";
import Modal from "./Modal.jsx";
import * as Sentry from "@sentry/react";
import "../../css/Main/LaunchTab.css";

const { ipcRenderer, shell } = window.require("electron");

function LaunchTab() {
  const [serverCount, setServerCount] = useState(null);
  const [versionInfo, setVersionInfo] = useState(null);
  const [userCount, setUserCount] = useState(null);
  const [loadingVersion, setLoadingVersion] = useState(true);
  const [loadingUserCount, setLoadingUserCount] = useState(true);
  const [loadingServerCount, setLoadingServerCount] = useState(true);
  const [versionError, setVersionError] = useState(null);
  const [userCountError, setUserCountError] = useState(null);
  const [serverCountError, setServerCountError] = useState(null);
  const [isGameLaunched, setIsGameLaunched] = useState(false);
  const [isAudioPlaying, setIsAudioPlaying] = useState(false);
  const [backgroundImage, setBackgroundImage] = useState(null);
  const [isModalVisible, setIsModalVisible] = useState(false);

  const audioUrl = "https://cdn.lunarfn.org/client/v3/assets/End.mp3";
  const audioRef = useRef(null);

  useEffect(() => {
    ipcRenderer.on("UpdateLoadingBar", (event, callback) => {
      if (Number(callback) === 100) {
        setTimeout(() => setIsModalVisible(false), 3000);
      }
    });

    ipcRenderer.once("BreakLaunching", (event, callback) => {
      setIsModalVisible(false);
      console.log(`Failed to start game, ${callback}`);
    });

    const fetchVersionInfo = async () => {
      setLoadingVersion(true);
      try {
        const { data } = await axios.get(
          "https://launcher-service.v3.prod.lunarfn.org/game/api/version/timeline"
        );
        setVersionInfo(data.current);
        setVersionError(null);
      } catch (err) {
        console.error(`Fetch Version Info Error: ${err}`);
        Sentry.captureException(err);
        setVersionError("Failed to fetch version info");
      } finally {
        setLoadingVersion(false);
        setLoadingServerCount(false);
      }
    };

    const fetchStatsCount = async () => {
      setLoadingUserCount(true);
      try {
        const { data } = await axios.get(
          "https://launcher-service.v3.prod.lunarfn.org/launcher/api/stats/prod"
        );
        setUserCount(data.clients);
        setServerCount(data.servers);
        setUserCountError(null);
        setServerCountError(null);
      } catch (error) {
        console.error(`Fetch User Count Error: ${error}`);
        Sentry.captureException(error);
        setUserCountError("Failed to fetch stats");
      } finally {
        setLoadingUserCount(false);
      }
    };

    const fetchBackgroundImage = async () => {
      try {
        const { data } = await axios.get(
          "https://launcher-service.v3.prod.lunarfn.org/launcher/api/states/prod"
        );

        if (data.background) {
          setBackgroundImage(data.background);
        }
      } catch (error) {
        console.error("Failed to fetch background image:", error);
        setBackgroundImage(
          "https://cdn.lunarfn.org/client/v3/assets/Season10Loadingscreen.jpg"
        );
      }
    };

    fetchVersionInfo();
    fetchStatsCount();
    fetchBackgroundImage();

    const intervalId = setInterval(() => fetchStatsCount(), 30000);
    return () => clearInterval(intervalId);
  }, []);

  useEffect(() => {
    const audio = audioRef.current;
    if (audio) {
      audio.volume = 0.1; // only 1.0 - 0.0

      audio
        .play()
        .then(() => {
          setIsAudioPlaying(true);
        })
        .catch((err) => {
          console.error("Failed to play audio:", err);
        });

      return () => {
        audio.pause();
        setIsAudioPlaying(false);
      };
    }
  }, []);

  const toggleAudio = () => {
    const audio = audioRef.current;
    if (isAudioPlaying) {
      audio.pause();
    } else {
      audio.play().catch((err) => {
        console.error("Failed to play audio:", err);
      });
    }
    setIsAudioPlaying(!isAudioPlaying);
  };

  const toggleGameLaunch = () => {
    if (isGameLaunched) {
      ipcRenderer.send("CloseGame");
    } else {
      ipcRenderer.send("StartGame", { version: versionInfo });
      setIsModalVisible(true);
    }
    setIsGameLaunched(!isGameLaunched);
  };

  useEffect(() => {
    let isAudioPausedDueToWindowState = false;

    const handleWindowState = () => {
      ipcRenderer.send("GetWindowState");
      ipcRenderer.once(
        "SendWindowState",
        (event, { isMinimized, isVisible, isFocused }) => {
          if (isMinimized || !isVisible || !isFocused) {
            if (isAudioPlaying) {
              audioRef.current.pause();
              setIsAudioPlaying(false);
              isAudioPausedDueToWindowState = true;
            }
          } else if (isAudioPausedDueToWindowState) {
            audioRef.current.play().catch((err) => {
              console.error("Failed to play audio:", err);
            });
            setIsAudioPlaying(true);
            isAudioPausedDueToWindowState = false;
          }
        }
      );
    };

    const intervalId = setInterval(handleWindowState, 1000);

    return () => clearInterval(intervalId);
  }, [isAudioPlaying]);

  return (
    <div
      className="Outoftime-container"
      style={{ backgroundImage: `url(${backgroundImage})` }}
    >
      <div className="user-count-badge">
        <i className="fa-solid fa-user fa-sm"></i>
        {loadingUserCount ? "Loading..." : userCountError ? "Error" : userCount}
      </div>

      <div
        className="server-count-badge"
        onClick={() => shell.openExternal("https://up.lunarfn.org/")}
      >
        <i className="fa-solid fa-server fa-sm"></i>
        {loadingServerCount
          ? "Loading..."
          : serverCountError
          ? "Error"
          : serverCount}
      </div>

      <div className="content">
        <h2 className="season-title">Season X</h2>
        <h1 className="Outoftime-title">Out of Time</h1>
        <p className="description">
          Season X, with the slogan <span className="bold">Out Of Time</span>,
          was the <span className="bold">Tenth</span> season of{" "}
          <span className="bold">Fortnite</span>.
        </p>
      </div>

      <div className="Launch-section">
        {loadingVersion ? (
          <span>Loading version info...</span>
        ) : versionError ? (
          <span className="error-message">{versionError}</span>
        ) : (
          <span className="fortnite-version">
            <span className="fortnite-title">Fortnite</span> {versionInfo}
          </span>
        )}
        <button onClick={toggleGameLaunch} className="button">
          {isGameLaunched ? "Close" : "Launch"}
        </button>
      </div>

      <Modal isVisible={isModalVisible} />

      <audio src={audioUrl} ref={audioRef} />

      <button className="audio-toggle-button" onClick={toggleAudio}>
        <i
          className={`fa-solid ${
            isAudioPlaying ? "fa-volume-up" : "fa-volume-mute"
          }`}
        />
      </button>
    </div>
  );
}

export default LaunchTab;
