import axios from "axios";

const { ipcRenderer } = window.require("electron");

//global.AthenaProfile = null;
//global.CommonCoreProfile = null;
//global.CommonPublicProfile = null;
global.AthenaEntry = null;

// ok lets use this insted
export async function DownloadAthenaEntry() {
    return new Promise((resolve, reject) => {
        ipcRenderer.send("GetAccount");
        ipcRenderer.once("SendAccount", async (event, callback) => {
            try {
                if (!global.AthenaEntry) {
                    console.log("[MCP-V2] Athena Entry is not available, re-downloading it..");
                    const { accountId } = callback;
                    const { data } = await axios.get(`https://launcher-service.v3.prod.lunarfn.org/user/api/entry/athena/${accountId}`);
                    global.AthenaEntry = data;
                    resolve(data);
                } else {
                    resolve(global.AthenaEntry);
                }
            } catch (err) {
                reject(err);
            }
        });
    });
}

