import React from "react";
import { motion } from "framer-motion";
import "../../css/Settings/InDevelopment.css";

function InDevelopment() {
    const InDevelopment = "In Development!"

    return (
      <div className="InDevelopmentContainer">
        <motion.div
          key="inDevelopment"
          initial={{ y: 100, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          exit={{ y: 100, opacity: 0 }}
          transition={{ type: "spring", bounce: 0.3, duration: 0.4 }}
          className="inDevelopment"
        >
          <span className="InDevelopment">
          <i class="fa-solid fa-triangle-exclamation"></i> {InDevelopment}
          </span>
        </motion.div>
      </div>
    );
}

export default InDevelopment;
