import React, { useEffect, useState } from "react";
import axios from "axios";
import * as Sentry from "@sentry/react";
import { motion, AnimatePresence } from "framer-motion"; 
import "../../css/Main/News.css";

const News = () => {
  const [news, setNews] = useState([]);
  const [currentNewsIndex, setCurrentNewsIndex] = useState(0);
  const [progress, setProgress] = useState(100);

  useEffect(() => {
    axios
      .get("https://launcher-service.v3.prod.lunarfn.org/launcher/api/news/prod")
      .then((resp) => setNews(resp.data))
      .catch((err) => {
        Sentry.captureException(`Catch Error: ${err}`)
      });
  }, []);

  useEffect(() => {
    if (news.length > 0) {
      const interval = setInterval(() => {
        setCurrentNewsIndex((prevIndex) => (prevIndex + 1) % news.length);
      }, 12000);

      return () => clearInterval(interval);
    }
  }, [news]);

  useEffect(() => {
    if (news.length > 0) {
      setProgress(100);
      const progressInterval = setInterval(() => {
        setProgress(prev => Math.max(prev - (100 / 120), 0));
      }, 100);

      return () => clearInterval(progressInterval);
    }
  }, [currentNewsIndex, news.length]);

  if (news.length === 0) {
    return <div className="newsContainer">Loading...</div>;
  }

  const currentNews = news[currentNewsIndex];

  return (
    <div className="newsContainer">
      <AnimatePresence>
        {currentNews && (
          <motion.div
            key={currentNews.id}
            initial={{ x: 150, opacity: 0 }}
            animate={{ x: 0, opacity: 1 }}
            exit={{ x: -150, opacity: 0 }}
            transition={{ type: "spring", bounce: 0.5, duration: 0.6 }}
            className="news"
          >
            <div className="newsLabel">News!</div>
            <img src={currentNews.image} alt={currentNews.title} />
            <div className="info">
              <h2>{currentNews.title}</h2>
              <p>{currentNews.body}</p>
              <div className="date">{currentNews.display_date}</div>
            </div>
            <div className="progress-bar">
              <div className="progress" style={{ width: `${progress}%` }}></div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
};

export default News;
