import React, { useEffect, useState } from "react";
import Frame from "../components/frame.jsx";
import Sidebar from "../components/Main/SideBar.jsx";
import Note from "../components/Main/Note.jsx";
import Friends from "../components/Main/Friends.jsx";
import ImportPath from "../components/Library/ImportPath.jsx";
import Downloadbtn from "../components/Library/Downloadbtn.jsx";
import LogsButton from "../components/Library/LogsButton.jsx";
import ImportedBuild from "../components/Library/ImportedBuild.jsx";
import { motion } from "framer-motion";

const { ipcRenderer } = window.require("electron");

export default function Library() {
  const [builds, setBuilds] = useState([]);
  useEffect(() => {
    ipcRenderer.send("GetInstalledBuilds");
    // dont use once here ofc
    ipcRenderer.on("InstalledBuilds", (event, callback) => {
      const { versions } = JSON.parse(callback);
      setBuilds(versions);
    });
  }, []);

  useEffect(() => {
    builds.forEach((versionObj) => {
      console.log("Build Object:", versionObj);
    });
  }, [builds]);

  const handleBuildDelete = (versionExtended) => {
    setBuilds((prevBuilds) =>
      prevBuilds.filter((build) => build.version_extended !== versionExtended)
    );
  };

  return (
    <div className="Main">
      <Frame />
      <Sidebar />
      <motion.div
        initial={{ x: 150, opacity: 0 }}
        animate={{ x: 0, opacity: 1 }}
        transition={{
          type: "spring",
          stiffness: 200,
          damping: 15,
          duration: 0.2,
        }}
        className="library-content"
      >
        <LogsButton />
        <Downloadbtn />
        <ImportPath />
        <Note />
        <Friends />
        <div className="builds-list">
          {builds.length > 0 ? (
            builds.map((build, index) => (
              <ImportedBuild
                key={index}
                input={build}
                onDelete={handleBuildDelete}
              />
            ))
          ) : (
            <p style={{ display: "none" }}>elise</p>
          )}
        </div>
      </motion.div>
    </div>
  );
}
