import React, { useState, useEffect } from "react";
import axios from "axios";
import * as Sentry from "@sentry/react";
import "../../css/Main/Note.css";

const Note = () => {
  const [MSG, setMSG] = useState(null); 

  useEffect(() => {
    const fetchVersion = async () => {
      try {
        const { data } = await axios.get(
          "https://launcher-service.v3.prod.lunarfn.org/launcher/api/states/prod"
        );

        if (data.emergency_message && data.emergency_message !== "null") {
          setMSG(data.emergency_message);
        } else {
          setMSG(null); 
        }
      } catch (err) {
        Sentry.captureException(`Try Error: ${err}`);
        setMSG("Error fetching message");
      }
    };

    fetchVersion();
  }, []);

  return (
    MSG && (
      <div className="note-container">
        <div className="note-content">
          <span className="note-text">
            <img
              className="warning-sign"
              src="https://cdn.lunarfn.org/client/v3/assets/emergency_message_v2.png"
              alt="Warning"
            />
          </span>
          <span className="note-info">{MSG}</span>
        </div>
      </div>
    )
  );
};

export default Note;
