import React from "react";
import "../../css/Library/Downloadbtn.css";

import { useNavigate } from "react-router-dom";

const Downloadbtn = () => {
    const navigate = useNavigate();

    return (
        <div className="Download-container" onClick={() => navigate("/downloads")} >
            <div className="Download-box">
                <div className="download-icon"> <i className="fa-solid fa-download fa-xl"></i> </div>
            </div>
            <div className="Download-text">Download</div>
            <div className="Download-subtext">Download a build</div>
        </div>
    );
};

export default Downloadbtn;
