import React, { useState } from "react";
import "../../css/Settings/CheckForUpdateButton.css";

import * as versioncheck from "../../utils/versioncheck.jsx";

const CheckForUpdateButton = () => {
  const [isChecking, setIsChecking] = useState(false);
  const [isDownloadingUpdate, setIsDownloadingUpdate] = useState(false);

  const handleClick = async () => {
    setIsChecking(true);

    setTimeout(async () => {
      const bIsOk = await versioncheck.CheckForUpdates();
      setIsChecking(false);
      setIsDownloadingUpdate(!bIsOk);
    }, 1000); // we want to let the user see the animation for a bit.
  };

  return (
    <button className="check-update-btn" onClick={handleClick} disabled={isChecking}>
      {isChecking ? (
        <>
          Checking for updates... <i className="fa-solid fa-rotate fa-spin"></i>
        </>
      ) : (
        <>
          {isDownloadingUpdate ? "Installing Updates.." : "Check for Updates!"} <i className="fas fa-sync-alt"></i>
        </>
      )}
    </button>
  );
};

export default CheckForUpdateButton;
