import React, { useEffect, useState } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import "../../css/Main/sidebar.css";
import HelpCard from "./HelpCard.jsx";

import * as MCP from "../../utils/MCP.jsx";
const { ipcRenderer } = window.require("electron");

const Sidebar = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const [characterId, setCharacterId] = useState(null);
  const [displayName, setDisplayName] = useState(null);

  useEffect(() => {
    const fetchCharacter = async () => {
      try {
        const entry = await MCP.DownloadAthenaEntry();
        const characterString = entry.character;
        const characterId = characterString.split(":")[1];
        setCharacterId(characterId);
      } catch (error) {
        console.error("Error fetching character:", error);
      }
    };

    const fetchAccount = () => {
      ipcRenderer.send("GetAccount");
      ipcRenderer.once("SendAccount", (event, callback) => {
        setDisplayName(callback.displayName);
      });
    };

    fetchAccount();
    fetchCharacter();
  }, []);

  // CID_001_Athena_Commando_F_Default
  const characterImageUrl = `https://fortnite-api.com/images/cosmetics/br/${characterId}/icon.png`

  const isActive = (path) => location.pathname === path;

  return (
    <div className="sidebar">
      <ul className="menu-list">
        <li
          className={`menu-item ${isActive("/main") ? "active" : ""}`}
          onClick={() => navigate("/main")}
        >
          <i className="fa-solid fa-house"></i>
          <span>Home</span>
        </li>
        <li
          className={`menu-item ${isActive("/library") ? "active" : ""}`}
          onClick={() => navigate("/library")}
        >
          <i className="fa-solid fa-box-open"></i>
          <span>Library</span>
        </li>
        <li
          className={`menu-item ${isActive("/downloads") ? "active" : ""}`}
          onClick={() => navigate("/downloads")}
        >
          <i className="fa-solid fa-download"></i>
          <span>Downloads</span>
        </li>
        <li
          className={`menu-item ${isActive("/leaderboard") ? "active" : ""}`}
          onClick={() => navigate("/leaderboard")}
        >
          <i className="fa-solid fa-ranking-star"></i>
          <span>LeaderBoard</span>
        </li>
      </ul>
      <HelpCard />
      <div className="profile-section">
        <img
          src={`https://fortnite-api.com/images/cosmetics/br/${characterId ? characterId : "CID_001_Athena_Commando_F_Default"}/icon.png`}
          alt="Profile"
          className="profile-image"
        />
        <div className="profile-info">
          <span className="profile-name">{displayName}</span>
        </div>

        <div
          onClick={() => navigate("/settings")}
          className={`Settings ${isActive("/settings") ? "active" : ""}`}
        >
          <i className="fa-solid fa-gear"></i>
        </div>
      </div>
    </div>
  );
};

export default Sidebar;