import React, { useEffect, useState } from "react";
import "../../css/Settings/LogoutHud.css";
import LogoutButton from "./Logout.jsx";

import * as MCP from "../../utils/MCP.jsx";
const { ipcRenderer } = window.require("electron");

const LogoutHud = () => {

  const [characterId, setCharacterId] = useState(null);
  const [displayName, setDisplayName] = useState(null);
  const [accountId, setaccountId] = useState(null);

  useEffect(() => {
    const fetchCharacter = async () => {
      try {
        const entry = await MCP.DownloadAthenaEntry();
        const characterString = entry.character;
        const characterId = characterString.split(":")[1];
        setCharacterId(characterId);
      } catch (error) {
        console.error("Error fetching character:", error);
      }
    };

    const fetchAccount = () => {
      ipcRenderer.send("GetAccount");
      ipcRenderer.once("SendAccount", (event, callback) => {
        setDisplayName(callback.displayName);
        setaccountId(callback.accountId);
      });
    };

    fetchAccount();
    fetchCharacter();
  }, []);

  return (
    <div className="logout-hud">
      <div className="logout-content">
        <div className="Profile">
          <img src={`https://fortnite-api.com/images/cosmetics/br/${characterId ? characterId : "CID_001_Athena_Commando_F_Default"}/icon.png`} alt="Profile" />
        </div>
        <div className="text-content">
          <h1>Hi, {displayName}</h1>
          <p>
            We Hope you are enjoying <span className="Bold" >Lunar!</span>
          </p>
        </div>
      </div>
      <p className="AccountID"> {accountId} </p>
      <LogoutButton />
    </div>
  );
};

export default LogoutHud;
