import React, { useEffect, useState } from "react";
import "../../css/Library/ImportedBuild.css";
import CircularProgress from "@mui/material/CircularProgress";
import { motion } from "framer-motion";
import axios from "axios";

const { ipcRenderer } = window.require("electron");

const ImportedBuild = ({ input, onDelete }) => {
  const [versionInfo, setVersionInfo] = useState({});
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    const fetch = async () => {
      const { data } = await axios.get(
        "https://launcher-service.v3.prod.lunarfn.org/game/api/data/all"
      );

      setVersionInfo(data[input.version_extended]);
    };

    fetch();
  }, []);

  const deleteBuild = () => {
    ipcRenderer.send("DeleteGame", input.version_extended);
    onDelete(input.version_extended);
  };

  const StartGame = () => {
    setLoading(true);
    ipcRenderer.send("StartGame", { version: input.version_extended });

    setTimeout(() => {
      setLoading(false);
    }, 5000);
  };

  return (
    <motion.div
      initial={{ x: 150, opacity: 0 }}
      animate={{ x: 0, opacity: 1 }}
      transition={{
        type: "spring",
        stiffness: 300,
        damping: 15,
        duration: 0.1,
      }}
      className="imported-build-container"
    >
      <div className="Imported-box">
        <div className="Splash">
          {versionInfo.image ? (
            <img src={versionInfo.image} alt="Splash" />
          ) : (
            <p>No Image Available</p>
          )}
        </div>
        <div className="favicon-icon" onClick={StartGame}>
          {loading ? (
            <CircularProgress
              sx={{ color: "#f5f5f5" }}
              className="Loading"
              size={18}
            />
          ) : (
            <i className="fa-solid fa-play" />
          )}
        </div>
      </div>
      <div className="Build-text">Fortnite {input.version}</div>
      <div className="Build-subtext">{input.version_extended}</div>
      <div className="delete-button">
        <i onClick={() => deleteBuild()} className="fa-solid fa-trash-can" />
      </div>
    </motion.div>
  );
};

export default ImportedBuild;
