import React from "react"
import Frame from "../components/frame.jsx";
import Sidebar from "../components/Main/SideBar.jsx";
import { motion } from "framer-motion";
import Note from "../components/Main/Note.jsx";
import Friends from "../components/Main/Friends.jsx";
import UserScore from "../components/Leaderboard/UserScore.jsx";
import LeaderboardHeader from "../components/Leaderboard/LeaderboardHeader.jsx";
import FriendsScore from "../components/Leaderboard/FriendsScore.jsx";
import Spacer from "../components/Leaderboard/Spacer.jsx";

export default function Leaderboards() {
    return (
        <div className="Main">
            <Frame />
            <Sidebar />
            <motion.div
        initial={{ x: 150, opacity: 0 }}
        animate={{ x: 0, opacity: 1 }}
        transition={{
          type: "spring",
          stiffness: 200,
          damping: 15,
          duration: 0.2,
        }}
        className="Leaderboards-content"
>
<Spacer /> 
<FriendsScore />
<LeaderboardHeader />
<UserScore />
<Note />
<Friends/>
</motion.div>
  </div>
    )
}