import React, { useEffect, useState } from "react";
import axios from "axios";
import { AnimatePresence, motion } from "framer-motion";
import "../../css/Main/MiniShop.css";
import ClockIcon from "./ClockIcon";
import * as MCP from "../../utils/MCP.jsx";

const vbucksImage = "https://cdn.lunarfn.org/client/v3/assets/Vbucks.png";

const MiniShop = () => {
  const [items, setItems] = useState([]);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [imageFailed, setImageFailed] = useState(false);
  const [progress, setProgress] = useState(100);
  const [timeLeft, setTimeLeft] = useState("");
  const [currency, setCurrency] = useState(0);

  const changeInterval = 6000;

  useEffect(() => {
    const fetchCurrency = async () => {
      const entry = await MCP.DownloadAthenaEntry();
      setCurrency(entry.currency);
    };
    fetchCurrency();
  }, []);

  useEffect(() => {
    const fetchItems = async () => {
      try {
        const response = await axios.get("https://api.v2.prod.lunarfn.org/fortnite/api/storefront/v2/catalog");
        const dailyStorefront = response.data.storefronts.find(storefront => storefront.name === "BRDailyStorefront");
        const weeklyStorefront = response.data.storefronts.find(storefront => storefront.name === "BRWeeklyStorefront");

        const combinedStorefronts = [...(dailyStorefront ? dailyStorefront.catalogEntries : []), ...(weeklyStorefront ? weeklyStorefront.catalogEntries : [])];

        if (combinedStorefronts.length > 0) {
          const itemsWithNames = await Promise.all(combinedStorefronts.map(async (item) => {
            try {
              const skinID = item.itemGrants[0].templateId.split(":")[1];
              const { data } = await axios.get(`https://fortnite-api.com/v2/cosmetics/br/${skinID}`);
              const gameplayTags = data.data.gameplayTags;
              const seasonTag = gameplayTags.find(tag => tag.startsWith("Cosmetics.Filter.Season."));
              const seasonNumber = seasonTag ? seasonTag.split("Cosmetics.Filter.Season.")[1] : "N/A";
              return {
                ...item,
                name: data.data.name,
                rarity: data.data.rarity.value,
                type: data.data.type.value,
                description: data.data.description,
                season: seasonNumber,
                imageUrl: `https://fortnite-api.com/images/cosmetics/br/${skinID}/featured.png`
              };
            } catch (error) {
              console.error(`Error fetching data for item: ${item.devName}`, error);
              return null;
            }
          }));

          const filteredItems = itemsWithNames.filter(item => item !== null);
          setItems(filteredItems);
        }
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    fetchItems();
  }, []);

  useEffect(() => {
    const interval = setInterval(() => {
      setProgress(prev => {
        if (prev <= 0) {
          setCurrentIndex(prevIndex => (prevIndex + 1) % items.length);
          setProgress(100);
          return 100;
        }
        return prev - 1;
      });
    }, changeInterval / 100);

    return () => clearInterval(interval);
  }, [items]);

  useEffect(() => {
    const timer = setInterval(() => {
      const now = new Date();
      const target = new Date();
      target.setHours(2, 0, 0, 0);
      if (now >= target) {
        target.setDate(target.getDate() + 1);
      }
      const diff = target - now;
      const hours = Math.floor(diff / (1000 * 60 * 60));
      const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
      const seconds = Math.floor((diff % (1000 * 60)) / 1000);
      setTimeLeft(`${hours.toString().padStart(2, "0")}:${minutes.toString().padStart(2, "0")}:${seconds.toString().padStart(2, "0")}`);
    }, 1000);

    return () => clearInterval(timer);
  }, []);

  useEffect(() => {
    setImageFailed(false);
  }, [currentIndex]);

  if (items.length === 0) return <div></div>;

  const currentItem = items[currentIndex];

  return (
    <div className="featured-shop-wrapper right">
      <AnimatePresence initial={false}>
        {items.length > 0 && (
          <motion.div
            key={currentItem.name}
            initial={{ y: 150, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            exit={{ y: -150, opacity: 0 }}
            transition={{ type: "spring", bounce: 0.5, duration: 0.6 }}
            className={`featured-shop shop-animate-event ${currentItem.rarity.toLowerCase()}`}
          >
            <div className="typeAndTime">
              <label className="itemType">{currentItem.rarity.toUpperCase()} {currentItem.type.toUpperCase()}</label>
              <label className="itemTime">
                <ClockIcon /> { }
                {timeLeft}
              </label>
            </div>
            <motion.div
              initial={{ y: 150, opacity: 0 }}
              animate={{ y: 0, opacity: 1 }}
              className={`cosmetic ${
                currentItem.type.toLowerCase() === "glider"
                  ? "glider"
                  : currentItem.type.toLowerCase() === "pickaxe"
                  ? "pickaxe"
                  : ""
              }`}
            >
              <h2 className="name">{currentItem.name}</h2>
              <small className="description">{currentItem.description || ""}</small>
              <small className="descriptionSeason">Season {currentItem.season}</small>
              {!imageFailed && (
                <img
                  src={currentItem.imageUrl}
                  className={`image ${
                    currentItem.type.toLowerCase() === "glider"
                      ? "glider-image"
                      : currentItem.type.toLowerCase() === "pickaxe"
                      ? "pickaxe-image"
                      : ""
                  }`}
                  draggable={false}
                  alt={currentItem.name}
                  onError={() => setImageFailed(true)}
                />
              )}
              {imageFailed && (
                <img
                  src={`https://fortnite-api.com/images/cosmetics/br/${currentItem.itemGrants[0].templateId.split(":")[1]}/icon.png`}
                  className={`image ${
                    currentItem.type.toLowerCase() === "glider"
                      ? "glider-image"
                      : currentItem.type.toLowerCase() === "pickaxe"
                      ? "pickaxe-image"
                      : ""
                  }`}
                  draggable={false}
                  alt={currentItem.name}
                />
              )}
            </motion.div>
            <div className="purchase">
              <img src={vbucksImage} className="vbucker" alt="V-Bucks" />
              {currentItem.prices[0].finalPrice === 0 ? (
                <span><strong>Free!</strong></span>
              ) : (
                <span style={{ fontSize: "1.2em", fontWeight: "Bold" }}>
                  <strong style={{ color: currency >= currentItem.prices[0].finalPrice ? "#9aec82e8" : "#EB7A58D1" }}>
                    {currency}
                  </strong> / {currentItem.prices[0].finalPrice}
                </span>
              )}
            </div>
            <div className="progress-bar">
              <div className="progress" style={{ width: `${progress}%` }}></div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
      <div className="selector">
        {items.map((_, index) => (
          <button
            className={`selector-item ${index === currentIndex ? "active" : ""}`}
            onClick={() => {
              setCurrentIndex(index);
              setProgress(100);
            }}
            key={index}
          />
        ))}
      </div>
    </div>
  );
};

export default MiniShop;