import React, { useEffect, useRef } from "react";

const ClockIcon = () => {
  const hourRef = useRef(null);
  const secondRef = useRef(null);

  useEffect(() => {
    const updateClock = () => {
      const now = new Date();
      const seconds = now.getSeconds();
      const minutes = now.getMinutes();
      const hours = now.getHours();

      const secondDeg = (seconds / 10) * 360;
      const hourDeg = (hours / 12) * 360 + (minutes / 60) * 30;

      if (secondRef.current) {
        secondRef.current.setAttribute("transform", `rotate(${secondDeg} 8 8)`);
      }
      if (hourRef.current) {
        hourRef.current.setAttribute("transform", `rotate(${hourDeg} 8 8)`);
      }
    };

    updateClock();
    const intervalId = setInterval(updateClock, 250);

    return () => clearInterval(intervalId);
  }, []);

  return (
    <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
      <circle cx="8" cy="8" r="7" fill="none" stroke="white" strokeWidth="2" />
      <line ref={hourRef} x1="8" y1="8" x2="8" y2="4.5" stroke="white" strokeWidth="2" strokeLinecap="round" />
      <line ref={secondRef} x1="8" y1="8" x2="8" y2="2.5" stroke="white" strokeWidth="2" strokeLinecap="round" />
      <circle cx="8" cy="8" r="1" fill="white" />
    </svg>
  );
};

export default ClockIcon;

