import React, { useEffect, useState } from "react";
import * as Sentry from "@sentry/react"
import "../../css/Main/BPCard.css";
import * as MCP from "../../utils/MCP.jsx";

const BattlePassCard = () => {
  const [bBattlePassPurchased, setBBattlePassPurchased] = useState(false);
  const [battlePassLevel, setBattlePassLevel] = useState(0);

  useEffect(() => {
    const fetchProfiles = async () => {
      try {
        const entry = await MCP.DownloadAthenaEntry()
        setBattlePassLevel(entry.battlepass_level);
        setBBattlePassPurchased(entry.battlepass_purchased);
      } catch (err) {
        Sentry.captureException(`Profile Fetch failed: ${err}`);
        console.error("Failed to fetch profiles:", err);
      }
    };

    fetchProfiles();
  }, []);

  const imageUrl = bBattlePassPurchased
    ? "https://cdn.lunarfn.org/client/v3/assets/BattlePassPurchased.png"
    : "https://cdn.lunarfn.org/client/v3/assets/BattlePass.png";

  const cardClass = bBattlePassPurchased ? "BattlePass-card-Purchased" : "BattlePass-card";
  const labelClass = bBattlePassPurchased ? "BattlePass-label-Purchased" : "BattlePass-label";
  const imageClass = bBattlePassPurchased ? "tier-image-Purchased" : "tier-image";

  return (
    <div className={cardClass}>
      <img src={imageUrl} alt="Battle Pass Tier" className={imageClass} />
      <span className="BattlePass-value">{battlePassLevel}</span>
      <div className={labelClass}>Battle Pass Tier</div>
    </div>
  );
};

export default BattlePassCard;
