import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { motion } from "framer-motion";
import { Particles } from "../components/effects.jsx";
import Frame from "../components/frame.jsx";

import "../css/auth.css";

const { shell, ipcRenderer } = window.require("electron");

export default function Auth() {
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(false);
  const [dots, setDots] = useState("");

  useEffect(() => {
    if (isLoading) {
      const interval = setInterval(() => {
        setDots((prev) => (prev.length < 3 ? prev + "." : ""));
      }, 500);
      return () => clearInterval(interval);
    }
  }, [isLoading]);

  useEffect(() => {
    ipcRenderer.once("LoginCall", (_event, data) => {
      if (data.success) {
        console.log("[Auth] continue to main page..");
        // change string to loggin in @criador_mods :D
        navigate("/main");
      }
    });
  }, [navigate]);

  useEffect(() => {
    ipcRenderer.send("AutoLogin");
    ipcRenderer.once("AutoLoginSuccess", (_event, data) => {
      if (data.success) navigate("/main");
    });
  }, [navigate]);

  return (
    <motion.div
      initial={{ x: 150, opacity: 0 }}
      animate={{ x: 0, opacity: 1 }}
      transition={{
        type: "spring",
        stiffness: 200,
        damping: 15,
        duration: 0.3,
      }}
      className="body"
    >
      <Frame />
      <div style={{ position: "absolute" }}>
        <Particles />
      </div>
      <div className="login-container">
        <div className="login-box">
          <h1>
            <img
              alt="LunarLogoV3"
              className="LunarLogo"
              src="https://cdn.lunarfn.org/client/v3/assets/LunarV3.png"
            />
            LUNAR
          </h1>
          <p className="explain">
            To use any of our in-game services, you must authenticate yourself
            via <span className="discord">Discord</span>.
          </p>
          <div
            onClick={() => {
              if (!isLoading) {
                shell.openExternal("https://auth.lunarfn.org/");
                setIsLoading(true);
              }
            }}
            className={`login-button ${isLoading ? "disabled" : ""}`}
            disabled={isLoading}
          >
            <p className="NextString">
              {isLoading ? `Waiting for a callback${dots}` : "Next"}
            </p>{" "}
            {isLoading ? (
              <i className="fa-solid fa-arrow-rotate-right fa-spin"></i>
            ) : (
              <i className="fa-solid fa-arrow-right" />
            )}
          </div>
          <p className="support-text">
            For help, visit our <a onClick={() => shell.openExternal("https://discord.gg/XERetZrk")}>Support Server </a>
          </p>
        </div>
      </div>
    </motion.div>
  );
}
