import React from "react";
import "../../css/Library/LogsButton.css";

const { ipcRenderer } = window.require("electron")

const LogsButton = () => {
    return (
        <div className="logs-button" onClick={() => ipcRenderer.send("OpenLogs")}>
            <span className="icon"></span>
            <span className="logs-text">Open Logs</span>
        </div>
    );
};

export default LogsButton;
