import React, { useEffect, useState } from "react";
import axios from "axios";
import "../../css/Main/Friends.css";

const { ipcRenderer } = window.require("electron");

const Friends = () => {
  const [friendAmount, setFriendAmount] = useState(0);
  const [onlineFriends, setOnlineFriends] = useState(0);

  const fetchFriendList = () => {
    ipcRenderer.send("GetAccount");
    ipcRenderer.once("SendAccount", async (event, account) => {
      const { access_token, accountId, online_friends } = account;
      
      setOnlineFriends(online_friends || 0);

      try {
        const req1 = await axios.get(
          `https://api.v2.prod.lunarfn.org/friends/api/public/friends/${accountId}?includePending=true`,
          { headers: { Authorization: `bearer ${access_token}` } }
        );

        let accountIds = [];
        let friendlist = [];

        req1.data.forEach((friendObj) => {
          const status = friendObj.status;

          if (status === "ACCEPTED") {
            accountIds.push(friendObj.accountId);
          }
        });

        const query = accountIds.map((id) => `accountId=${id}`).join("&");

        if (query) {
          const req2 = await axios.get(
            `https://api.v2.prod.lunarfn.org/account/api/public/account?${query}`,
            { headers: { Authorization: `bearer ${access_token}` } }
          );

          req2.data.forEach((obj) => {
            friendlist.push({
              [obj.id]: { displayName: obj.displayName },
            });
          });
        }

        setFriendAmount(friendlist.length);
      } catch (error) {
        console.error("Error fetching friend list:", error);
        setFriendAmount(0); 
        setOnlineFriends(0);
      }
    });
  };

  useEffect(() => {
    
    fetchFriendList();
    const interval = setInterval(() => {
      fetchFriendList();
    }, 10 * 60 * 1000); 

    return () => clearInterval(interval); 
  }, []);

  return (
    <div className="friends-count">
      <i className="fa-solid fa-user-group" />
      <span className="Online-friends">{onlineFriends}</span>
      <span className="separator">/</span>
      <span className="current-friends">{friendAmount}</span>
    </div>
  );
};

export default Friends;
