import React, { useState, useEffect } from "react";
import "../../css/Main/WinsCard.css";

import * as MCP from "../../utils/MCP.jsx"

const WinsCard = () => {
  const [wins, setWins] = useState(0);

  useEffect(() => {
    const fetchMatched = async () => {
      const data = await MCP.DownloadAthenaEntry();
      setWins(data.victories);
    }

    fetchMatched();
  }, [])

  return (
    <div className="Wins-card">
      <span className="Wins-value">{wins}</span>
      <div className="Wins-label">Lifetime Wins</div>
      <div className="Wins-icon">
        <i className="fa-solid fa-trophy"></i>
      </div>
    </div>
  );
};

export default WinsCard;
