import React, { useEffect, useRef } from "react";

export function Particles() {
  const canvasRef = useRef(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    const ctx = canvas.getContext("2d");
    const particlesArray = [];

    const handleResize = () => {
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
    };

    const initCanvas = () => {
      handleResize();
      for (let i = 0; i < 20; i++) {
        particlesArray.push(new Particle());
      }
      ctx.shadowBlur = 3;
      ctx.shadowColor = "#3c3770";
      animate();
    };

    class Particle {
      constructor() {
        this.size = Math.random() * 20 + 15;
        this.x = Math.random() * canvas.width;
        this.y = Math.random() * canvas.height;
        this.speedX = Math.random() * 0.5 - 0.25;
        this.speedY = -(Math.random() * 0.5 + 0.25); // Negative speed to move upwards
        this.rotation = Math.random() * 360;
        this.rotationSpeed = Math.random() * 1.5 - 0.75;
      }

      draw() {
        const radius = 4;
        ctx.save();
        ctx.translate(this.x, this.y);
        ctx.rotate((this.rotation * Math.PI) / 180);
        ctx.fillStyle = "#3c3770";
        ctx.beginPath();
        ctx.moveTo(-this.size / 2 + radius, -this.size / 2);
        ctx.arcTo(
          this.size / 2,
          -this.size / 2,
          this.size / 2,
          this.size / 2,
          radius
        );
        ctx.arcTo(
          this.size / 2,
          this.size / 2,
          -this.size / 2,
          this.size / 2,
          radius
        );
        ctx.arcTo(
          -this.size / 2,
          this.size / 2,
          -this.size / 2,
          -this.size / 2,
          radius
        );
        ctx.arcTo(
          -this.size / 2,
          -this.size / 2,
          this.size / 2,
          -this.size / 2,
          radius
        );
        ctx.closePath();
        ctx.fill();
        ctx.restore();
      }

      update() {
        this.x += this.speedX;
        this.y += this.speedY;
        this.rotation += this.rotationSpeed;

        // Reset particle position if it goes off screen
        if (this.x + this.size < 0 || this.x - this.size > canvas.width) {
          this.x = Math.random() * canvas.width;
          this.y = canvas.height + this.size;
        }
        if (this.y + this.size < 0 || this.y - this.size > canvas.height) {
          this.x = Math.random() * canvas.width;
          this.y = canvas.height + this.size;
        }
      }
    }

    const animate = () => {
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      particlesArray.forEach((particle) => {
        particle.update();
        particle.draw();
      });
      requestAnimationFrame(animate);
    };

    window.addEventListener("resize", handleResize);
    initCanvas();

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  return <canvas ref={canvasRef} id="particleCanvas" />;
}
