import React, { useEffect, useState } from "react";
import "../../css/Settings/ClearButton.css";

const { ipcRenderer } = window.require("electron")

const ClearButton = () => {
  const [running, setRunning] = useState(false);
  const [done, setDone] = useState(false);

  const clearData = () => {
    ipcRenderer.send("ClearLauncherData");
    setRunning(true);
    setDone(false);

    ipcRenderer.once("ClearedLauncherData", (event, callback) => {
      setRunning(false);
      setDone(true);

      setTimeout(() => {
        setRunning(false);
        setDone(false);
      }, 3000);
    });
  }

  return (
    <div className="Clear-button-container">
      <div className="Clear-text" onClick={() => clearData()}>{running ? "Clearing Data.." : done ? "Cleared Data" : "Clear Launcher Data"}</div>
      <div className="Clear-icon">
        <i className="fa-solid fa-broom"></i>
      </div>
    </div>
  );
};

export default ClearButton;
