import React from "react";
import "../../css/Library/ImportPath.css";

const { ipcRenderer } = window.require("electron");

const ImportPath = () => {
  const find = () => {
    ipcRenderer.send("FindFortnitePath");
    ipcRenderer.once("DetectedFortnitePath", (event, callback) => {
      if (callback === "SUCCESS") ipcRenderer.send("GetInstalledBuilds");
    });
  };
  return (
    <div className="container">
      <div className="box" onClick={() => find()}>
        <div className="plus">+</div>
      </div>
      <div className="text">Import</div>
      <div className="subtext">Add existing installation</div>
    </div>
  );
};

export default ImportPath;
