import React, { useEffect, useState } from "react";
import "../../css/Main/KillsCard.css";
import * as MCP from "../../utils/MCP.jsx";

const KillsCard = () => {
  const [kd, setKd] = useState(0);

  useEffect(() => {
    const fetchData = async () => {
      const entry = await MCP.DownloadAthenaEntry();
      const calculated = entry.eliminations / entry.deaths;
      setKd(entry.eliminations === 0 ? 0 : calculated === Infinity ? entry.eliminations : calculated);
    };
    fetchData();
  }, []);

  return (
    <div className="stat-card">
      <span className="stat-value">{kd}</span>
      <div className="stat-label">Kill/Death Ratio</div>
      <div className="stat-icon">
        <i className="fa-solid fa-skull"></i>
      </div>
    </div>
  );
};

export default KillsCard;