import axios from "axios";
import * as Sentry from "@sentry/react"

const { ipcRenderer } = window.require("electron");

export async function CheckForUpdates() {
  try {
    const { data } = await axios.get(
      "https://launcher-service.v3.prod.lunarfn.org/launcher/api/versioncheck/prod"
    );

    ipcRenderer.send("VersionInfo");
    return new Promise((resolve) => {
      ipcRenderer.once("SendVersionInfo", (event, callback) => {
        const bIsOk = callback === data.current;

        if (!bIsOk) {
          ipcRenderer.send("InstallSoftUpdate", { version: data.current });
        }

        resolve(bIsOk);
      });
    });
  } catch (err) {
    Sentry.captureException(`error while versioncheck: ${err}`)
    return false;
  }
}

