import React from "react";
import { motion } from "framer-motion";
import { Particles } from "../components/effects.jsx";
import Frame from "../components/frame.jsx";

import "../css/offline.css";

const { shell } = window.require("electron");

export default function Offline() {
  return (
    <motion.div
      initial={{ x: 150, opacity: 0 }}
      animate={{ x: 0, opacity: 1 }}
      transition={{
        type: "spring",
        stiffness: 200,
        damping: 15,
        duration: 0.3,
      }}
      className="body"
    >
      <Frame />
      <div style={{ position: "absolute" }}>
        <Particles />
        <div className="offline-container">
      <div className="offline-box">
        <h1>SERVICES UNREACHABLE</h1>
        <p className="offline-explain">
          Looks like our Servers are <span className="outdated">Offline!</span> please check our <span className="discord">Discord</span> for News about the situation.
        </p>
        <div className="offline-button"
        onClick={() => {
            {
              shell.openExternal("https://discord.gg/lunarmp");
            }
          }}>
          <div className="Update">Discord</div>
          <i className="fa-brands fa-discord"></i>
        </div>
        <p className="offline-support-text">
           For help, visit our <a onClick={() => shell.openExternal("https://discord.gg/XERetZrk")}>Support Server </a>
        </p>
      </div>
    </div>
   </div>
    </motion.div>
  );
}