import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import CircularProgress from "@mui/material/CircularProgress";

import "../css/versioncheck.css";

import { Particles } from "../components/effects.jsx";
import Frame from "../components/frame.jsx";
import * as versioncheck from "../utils/versioncheck.jsx";

export default function Versioncheck() {
  const [status, setStatus] = useState("Checking For Updates");
  const [updateType, setUpdateType] = useState("PENDING");
  const [checkingAnimation, setCheckingAnimation] = useState("");
  const [bPlayDotAnimation, setBPlayDotAnimation] = useState(true);
  const [downloadingAnimation, setDownloadingAnimation] = useState("");

  const navigate = useNavigate();

  useEffect(() => {
    const UpdateChecking = async () => {
      setTimeout(async () => {
        const bUpdateOk = await versioncheck.CheckForUpdates();
        console.log(bUpdateOk);
        if (!bUpdateOk) {
          setStatus("Downloading Update");
          setUpdateType("SOFT_UPDATE");
        } else {
          setUpdateType("NO_UPDATE");
        }
      }, 1000);
    };

    UpdateChecking();
  }, []);

  useEffect(() => {
    if (bPlayDotAnimation) {
      const interval = setInterval(() => {
        setCheckingAnimation((prev) => {
          if (prev === "...") return ".";
          return prev + ".";
        });
      }, 400);
      return () => clearInterval(interval);
    }
  }, [bPlayDotAnimation]);

  useEffect(() => {
    if (updateType === "SOFT_UPDATE") {
      const interval = setInterval(() => {
        setDownloadingAnimation((prev) => {
          if (prev === "...") return ".";
          return prev + ".";
        });
      }, 400);
      return () => clearInterval(interval);
    } else if (updateType === "NO_UPDATE") {
      setBPlayDotAnimation(false);
      setTimeout(() => navigate("/auth"), 1000);
    } else if (updateType === "PENDING") {
      console.log(
        "[Versioncheck] PENDING update detected. Function will be execute again!"
      );
    } else {
      console.error(
        `Unknown Update type provided:\nExpected NO_UPDATE / SOFT_UPDATE / PENDING but got: ${updateType}`
      );
    }
  }, [navigate, updateType]);

  return (
    <div className="body">
      <Frame />
      <Particles />
      <div className="Container">
        <div className="Box">
          <center>
            <div className="StringDisplay">
              {status === "Checking For Updates" ? (
                <p className="StringToDisplay">
                  Checking For Updates{checkingAnimation}
                </p>
              ) : (
                <x className="StringToDisplay">
                  {status}
                  {downloadingAnimation}
                </x>
              )}
              <CircularProgress
                className="LoadingCircle"
                sx={{ color: "#f5f5f5" }}
              />
            </div>
          </center>
        </div>
      </div>
    </div>
  );
}
