import React, { useEffect, useState } from "react";
import "../../css/Leaderboard/UserScore.css";

import * as MCP from "../../utils/MCP.jsx";
const { ipcRenderer } = window.require("electron");

const UserScore = () => {
    const [Hype, setHype] = useState(0);
    const [displayName, setDisplayName] = useState(null);
    const [characterId, setCharacterId] = useState(null);

    useEffect(() => {
        const fetchCharacter = async () => {
          try {
            const entry = await MCP.DownloadAthenaEntry();
            const characterString = entry.character;
            const characterId = characterString.split(":")[1];
            setCharacterId(characterId);
          } catch (error) {
            console.error("Error fetching character:", error);
          }
        };
    
        const fetchAccount = () => {
          ipcRenderer.send("GetAccount");
          ipcRenderer.once("SendAccount", (event, callback) => {
            setDisplayName(callback.displayName);
          });
        };
    
        fetchAccount();
    }, []);

    useEffect(() => {
      const fetchData = async () => {
        const entry = await MCP.DownloadAthenaEntry();
        setHype(entry.leaderboard_score);
      };
      fetchData();
    }, []); 


  return (
    <div className="user-score">
      <div className="user-info">
        <span className="rank">#(Users place in the leaderboard)</span>
        <span className="username">{displayName}</span>
      </div>
      <div className="stats">
        <div className="stat">
          <span className="Score" ><i className="fas fa-fire"></i> {Hype}</span> 
        </div>
      </div>
    </div>
  );
};

export default UserScore;
